.main__div {
    width: 100%;
    height: 100vh;
    background: #9555b1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    
}
.con__div{
    
    height: 80vh;
    background: white;
    box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
    border-radius: 15px;
}
h1 {
    color: white;
    background: thistle;
    text-shadow: 0 2px black;
    text-align: center;
}

input{
    width: 50%;
    padding: 10px 20px;
    border:2px solid rgb(221, 220, 220);
    outline: none;
    margin-left: 20px;
    text-align: center;
    border-radius: 20px;
    
}

.li__div{
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
    
}



ol li{
text-align: left;
font-size: 20px;
font-weight:500;
min-height:40px;
display: flex;
align-items:center;
color:#8566aa;
text-transform: capitalize;
}

.cont {
    background-color: white;
   
}
.icnbtn:hover{
    transform: scale(2);
    
}
.lbtn:hover{
    color: crimson;
}